import _ from 'lodash'
import React, { useEffect, useRef, useState } from 'react'
import { Link } from 'react-router-dom'
import { Container, Icon, Label } from 'semantic-ui-react'
import { getSpec } from './util'

function Examples({ specs, lang, onClick }) {
  const [ examples, setExamples ] = useState([])

  const shuffle = () => {
    const spec = getSpec(specs, lang)
    if (spec !== null) {
      setExamples(_.sampleSize(spec.test, 5))
    }
  }

  const prevLang = useRef()
  useEffect(() => {
    // Check lang dependency manually to hide the dependency with specs.
    if (prevLang.current !== lang) {
      shuffle()
      prevLang.current = lang
    }
  })

  return (
    <Container className="examples">
      <label></label> <Icon name="shuffle" link onClick={shuffle} />
      {examples.map((x, i) => {
        return (
          <Label as={Link} to={`?lang=${lang}&word=${x.word}`} key={i}>
            {x.word}
          </Label>
        )
      })}
    </Container>
  )
}

export default Examples
